import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { fadeInRight400ms, fadeInUp400ms, scaleIn400ms, stagger40ms } from '@vex/animations';
import { Link } from '@vex/interfaces/link.interface';

import { OrganizationResource } from '@core/http/organization-resource.service';
import { Organization, Project } from '@data/schema';
import { OrganizationsCreateComponent } from '@organizations/components/organizations-create/organizations-create.component';
import { OrganizationDataService } from '@organizations/services/organization-data.service';

import { LayoutConfigService } from '@layout/services/layout-config.service';
import { OrganizationSettingsIcons } from './services/organization-settings-icons.service';

@Component({
  selector: 'organization-settings',
  templateUrl: './organization-settings.component.html',
  styleUrls: ['./organization-settings.component.scss'],
  animations: [
    fadeInUp400ms,
    fadeInRight400ms,
    scaleIn400ms,
    stagger40ms,
  ],
})
export class OrganizationSettingsComponent implements OnInit, OnDestroy {
  organization: Organization;
  projects: Project[];

  links: Link[] = [
    {
      label: 'MEMBERS',
      route: './',
    },
    {
      label: 'BILLING',
      route: '',
      disabled: false,
    },
  ];

  constructor(
    public icons: OrganizationSettingsIcons,
    private dialog: MatDialog,
    private layoutConfigService: LayoutConfigService,
    private organizationResource: OrganizationResource,
    private organizationDataService: OrganizationDataService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.layoutConfigService.setIkaros();
  }

  ngOnInit() {
    this.organizationDataService.organization$.subscribe(organization => {
      this.organization = new Organization(organization);
    });

    this.projects =  this.route.snapshot.data.projects;
  }

  scenariosPath() {
    return '/scenarios';
  }

  projectQueryParams(project) {
    return {
      project_id: project.id,
    };
  }

  openCreateOrganizationDialog() {
    const dialogRef = this.dialog.open(OrganizationsCreateComponent, {
      width: '600px',
      data: this.organization,
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
      this.updateOrganization($event);
    });

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

  updateOrganization(data) {
    this.organizationResource.update(this.organization.id, data).subscribe(
      (res: Organization) => {
        this.organization = res;
        this.organizationDataService.set(res);
      },
      err => {

      });
  }

  ngOnDestroy() {
    this.layoutConfigService.setZeus();
  }
}
