import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { fadeInRight400ms, fadeInUp400ms, scaleIn400ms, stagger40ms } from '@vex/animations';
import { Link } from '@vex/interfaces/link.interface';

import { Organization, PaymentMethod, Project, Subscription } from '@data/schema';
import { OrganizationDataService } from '@organizations/services/organization-data.service';
import { PaymentHistory } from '@shared/components/payment/payment-history/payment-history';

import { LayoutConfigService } from '@layout/services/layout-config.service';
import { OrganizationBillingIcons } from './services/organization-billing-icons.service';

@Component({
  selector: 'organization-billing',
  templateUrl: './organization-billing.component.html',
  styleUrls: ['./organization-billing.component.scss'],
  animations: [
    fadeInUp400ms,
    fadeInRight400ms,
    scaleIn400ms,
    stagger40ms,
  ],
})
export class OrganizationBillingComponent implements OnInit, OnDestroy {
  organization: Organization;
  projects: Project[];
  subscription: Subscription;
  paymentMethods: PaymentMethod[];
  payments: PaymentHistory[];

  constructor(
    public icons: OrganizationBillingIcons,
    private layoutConfigService: LayoutConfigService,
    private route: ActivatedRoute,
    private router: Router,
    private organizationDataService: OrganizationDataService
  ) {
    this.layoutConfigService.setIkaros();
  }

  ngOnInit() {
    this.payments = this.route.snapshot.data.payments;
    this.paymentMethods = this.route.snapshot.data.paymentMethods
      .map(paymentMethod => {
        return new PaymentMethod(paymentMethod);
      })
      .sort((a, b) => {
        if (b.isDefault) {
          return 1;
        }

        if (!b.isDefault) {
          return -1;
        }

        return 0;
      });

    this.subscription = this.route.snapshot.data.subscription;

    this.organizationDataService.organization$.subscribe(organization => {
      this.organization = organization;
    });
  }

  scenariosPath() {
    return '/scenarios';
  }

  update() {
    const path = '/subscriptions/pricing';

    this.router.navigate([path], {
      queryParams: { organization_id: this.organization.id },
    });
  }

  onPaymentMethodRemove(paymentMethodId: PaymentMethod['id']) {
    const newPaymentMethods = this.paymentMethods.slice();

    newPaymentMethods.splice(
      this.paymentMethods.findIndex((paymentMethod) => {
        return paymentMethod.id === paymentMethodId;
      }), 1);

    this.paymentMethods = newPaymentMethods;
  }

  ngOnDestroy() {
    this.layoutConfigService.setZeus();
  }
}
