import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { OrganizationsUser } from '@data/schema/organizations-user';

@Injectable({
  providedIn: 'root',
})
export class OrganizationMembersDataService {
  private subject = new BehaviorSubject(null);
  members$ = this.subject.asObservable();
  members: OrganizationsUser[];

  constructor() { }

  set(members: OrganizationsUser[]) {
    this.members = members;
    this.subject.next(members);
  }

  add(member: OrganizationsUser) {
    this.members.unshift(new OrganizationsUser(member));
    this.set(this.members);
  }

  delete(id: number) {
    this.members.splice(
      this.members.findIndex((existingOrganizationsUser) => {
        return existingOrganizationsUser.id === id;
      }), 1);

    this.set(this.members);
  }
}
