import { SelectionModel } from '@angular/cdk/collections';
import { Location } from '@angular/common';
import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { MatPaginator } from '@angular/material/paginator';
import { MatSelectChange } from '@angular/material/select';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { untilDestroyed } from 'ngx-take-until-destroy';
import { filter } from 'rxjs/operators';

import { fadeInUp400ms, stagger40ms } from '@vex/animations';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { OrganizationsUserResource } from '@core/http/organizations-user-resource.service';
import { FileService } from '@core/utils/file.service';
import { OrganizationsUser } from '@data/schema';
import { LayoutConfigService } from '@layout/services/layout-config.service';
import { OrganizationMembersCreateComponent } from './components/organization-members-create/organization-members-create.component';
import { aioTableData, aioTableLabels } from './mock/organization-members-data';
import { OrganizationMembersDataService } from './services/organization-members-data.service';
import { OrganizationMembersIcons } from './services/organization-members-icons.service';
import { OrganizationsUserRoleData } from './services/organizations-user-role-data.service';

@Component({
  selector: 'organization-members',
  templateUrl: './organization-members.component.html',
  styleUrls: ['./organization-members.component.scss'],
  animations: [
    fadeInUp400ms,
    stagger40ms,
  ],
  providers: [
    {
      provide: MAT_FORM_FIELD_DEFAULT_OPTIONS,
      useValue: {
        appearance: 'standard',
      } as MatFormFieldDefaultOptions,
    },
  ],
})
export class OrganizationMembersComponent implements OnInit, AfterViewInit, OnDestroy {

  layoutCtrl = new FormControl('boxed');

  // Table settings
  @Input()
  columns: TableColumn<OrganizationsUser>[] = [
    { label: 'Checkbox', property: 'checkbox', type: 'checkbox', visible: true },
    { label: 'Role', property: 'role', type: 'dropdown', visible: true},
    { label: 'Name', property: 'name', type: 'text', visible: true},
    { label: 'Email', property: 'email', type: 'text', visible: true},
  ];
  pageIndex = this.route.snapshot.queryParams.page || 0;
  pageSize = 10;
  pageSizeOptions: number[] = [5, 10, 20, 50];
  dataSource: MatTableDataSource<OrganizationsUser> | null;
  selection = new SelectionModel<OrganizationsUser>(true, []);
  searchCtrl = new FormControl();
  labels = aioTableLabels;

  // Breadcrumb settings
  crumbs = [{
    name: 'OrganizationsUsers',
  }];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    public icons: OrganizationMembersIcons,
    public organizationsUserRoleData: OrganizationsUserRoleData,
    private activatedRoute: ActivatedRoute,
    private dialog: MatDialog,
    private file: FileService,
    private layoutConfigService: LayoutConfigService,
    private location: Location,
    private organizationsUserResource: OrganizationsUserResource,
    private organizationMembersDataService: OrganizationMembersDataService,
    private router: Router,
    private route: ActivatedRoute
  ) {
    this.layoutConfigService.setIkaros();
  }

  get visibleColumns() {
    return this.columns.filter(column => column.visible).map(column => column.property);
  }

  ngOnInit() {
    this.dataSource = new MatTableDataSource();
    this.organizationMembersDataService.members$.subscribe((members: OrganizationsUser[]) => {
      this.dataSource.data = members;
    });

    const organizationMembers = this.route.snapshot.data.organizationMembers.map(member => {
      return new OrganizationsUser(member);
    });
    this.organizationMembersDataService.set(organizationMembers);

    this.searchCtrl.valueChanges.pipe(
      untilDestroyed(this)
    ).subscribe(value => this.onFilterChange(value));
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  getOrganizationsUsers(params) {
    const organizationId = this.getOrganizationId();

    this.organizationsUserResource.index(
      organizationId, params
    ).subscribe(
      (res: OrganizationsUser[]) => {
        this.organizationMembersDataService.set(res);
      },
      error => {

      }
    );
  }

  createOrganizationsUser(data: FormData) {
    const organizationId = this.getOrganizationId();

    this.organizationsUserResource.create(
      organizationId, data
    ).subscribe(
      res => {
        const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

        this.getOrganizationsUsers(organizationId);
      },
      error => {

      }
    );
  }

  showOrganizationsUser(member: OrganizationsUser) {
    const path: string = this.file.join('/', 'users', member.id, 'profile');
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.router.navigate([path]);
  }

  destroyOrganizationsUser(member: OrganizationsUser) {
    const organizationId = this.getOrganizationId();

    this.organizationsUserResource.destroy(
      organizationId, member.id
    ).subscribe(res => {
      this.organizationMembersDataService.delete(member.id);
      this.selection.deselect(member);
    });
  }

  destroyOrganizationsUsers(organizationMembers: OrganizationsUser[]) {
    organizationMembers.forEach(r => this.destroyOrganizationsUser(r));
  }

  updateOrganizationsUser(member: OrganizationsUser, params: object) {
    const organizationId = this.getOrganizationId();

    this.organizationsUserResource.update(
      organizationId, member.id, params
    ).subscribe(res => {
      for (const key in params) {
        if (params.hasOwnProperty(key)) {
          member[key] = params[key];
        }
      }
    });
  }

  /**
   *
   * Table methods
   *
   */
  openCreateDialog() {
    const dialogRef = this.dialog.open(OrganizationMembersCreateComponent, {
      width: '600px',
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(
      ($event) => {
        this.createOrganizationsUser($event);
      }
    );

    dialogRef.afterClosed().subscribe((member: OrganizationsUser) => {
      onCreateSub.unsubscribe();
    });
  }

  onFilterChange(value: string) {
    if (!this.dataSource) {
      return;
    }
    value = value.trim();
    value = value.toLowerCase();
    this.dataSource.filter = value;
  }

  toggleColumnVisibility(column, event) {
    event.stopPropagation();
    event.stopImmediatePropagation();
    column.visible = !column.visible;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  trackByProperty<T>(index: number, column: TableColumn<T>) {
    return column.property;
  }

  /**
   * On page change, add queryParam 'page' to URL
   */
  onPaginateChange($event) {
    const queryParams = Object.assign({}, this.route.snapshot.queryParams);
    queryParams.page = $event.pageIndex;

    const url = this
        .router
        .createUrlTree([], {relativeTo: this.activatedRoute, queryParams})
        .toString();

    this.location.go(url);
  }

  getOrganizationId() {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const organizationId = snapshot.parent.params.organizationId;
    return organizationId;
  }

  ngOnDestroy() {
    this.layoutConfigService.setZeus();
  }
}
