import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import icAccessTime from '@iconify/icons-ic/twotone-access-time';
import icAdd from '@iconify/icons-ic/twotone-add';
import icCheck from '@iconify/icons-ic/twotone-check';
import icMail from '@iconify/icons-ic/twotone-mail';
import icPersonAdd from '@iconify/icons-ic/twotone-person-add';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icWhatshot from '@iconify/icons-ic/twotone-whatshot';
import icWork from '@iconify/icons-ic/twotone-work';

import { fadeInRight400ms, fadeInUp400ms, scaleIn400ms, stagger40ms } from '@vex/animations';
import { Link } from '@vex/interfaces/link.interface';

import { Organization } from '@data/schema/organization';
import { OrganizationDataService } from './services/organization-data.service';

@Component({
  selector: 'organizations',
  templateUrl: './organizations.component.html',
  styleUrls: ['./organizations.component.scss'],
  animations: [
    fadeInUp400ms,
    fadeInRight400ms,
    scaleIn400ms,
    stagger40ms,
  ],
})
export class OrganizationsComponent implements OnInit {
  organization: Organization;

  links: Link[] = [
    {
      label: 'SETTINGS',
      route: './settings',
      active: () => this.isActive('settings'),
    },
    {
      label: 'MEMBERS',
      route: './members',
      disabled: false,
      active: () => this.isActive('members'),
    },
    {
      label: 'BILLING',
      route: './billing',
      disabled: false,
      active: () => this.isActive('billing'),
    },
  ];

  icWork = icWork;
  icPhone = icPhone;
  icPersonAdd = icPersonAdd;
  icCheck = icCheck;
  icMail = icMail;
  icAccessTime = icAccessTime;
  icAdd = icAdd;
  icWhatshot = icWhatshot;

  constructor(
    private route: ActivatedRoute,
    private organizationDataService: OrganizationDataService
  ) { }

  ngOnInit() {
    this.organization = this.route.snapshot.data.organization;
    this.organizationDataService.set(this.organization);
  }

  isActive(path: string) {
    return this.route.firstChild.routeConfig.path === path;
  }
}
