import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { of } from 'rxjs';

import { OrganizationResource } from '@core/http/organization-resource.service';

@Injectable({
  providedIn: 'root',
})
export class OrganizationSubscriptionResolver implements Resolve<any> {

  constructor(
    private organizationResource: OrganizationResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    let organization_id: number = route.queryParams.organization_id;

    if (!organization_id) {
      organization_id = route.parent.params.organization_id;
    }

    if (!organization_id) {
      return of(null);
    }

    return this.organizationResource.subscription(organization_id);
  }
}
