import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { VexRoutes } from '@vex/interfaces/vex-route.interface';

import { ProjectsResolver } from '@projects/services/projects-resolver.service';

import { OrganizationsComponent } from './organizations.component';
import { OrganizationBillingComponent } from './pages/organization-billing/organization-billing.component';
import { OrganizationMembersComponent } from './pages/organization-members/organization-members.component';
import { OrganizationsUsersResolver } from './pages/organization-members/services/organizations-users-resolver.service';
import { OrganizationSettingsComponent } from './pages/organization-settings/organization-settings.component';
import { OrganizationPaymentMethodsResolver } from './services/organization-payment-methods-resolver.service';
import { OrganizationPaymentsResolver } from './services/organization-payments-resolver.service';
import { OrganizationResolver } from './services/organization-resolver.service';
import { OrganizationSubscriptionResolver } from './services/organization-subscription-resolver.service';


const routes: VexRoutes = [
  {
    path: ':organization_id',
    component: OrganizationsComponent,
    data: {
      toolbarShadowEnabled: true,
      containerEnabled: true,
    },
    resolve: {
      organization: OrganizationResolver,
    },
    children: [
      {
        path: '',
        component: OrganizationSettingsComponent,
        resolve: {
          projects: ProjectsResolver,
        },
      },
      {
        path: 'settings',
        component: OrganizationSettingsComponent,
        resolve: {
          projects: ProjectsResolver,
        },
      },
      {
        path: 'members',
        component: OrganizationMembersComponent,
        resolve: {
          organizationMembers: OrganizationsUsersResolver,
        },
      },
      {
        path: 'billing',
        component:  OrganizationBillingComponent,
        resolve: {
          projects: ProjectsResolver,
          subscription: OrganizationSubscriptionResolver,
          payments: OrganizationPaymentsResolver,
          paymentMethods: OrganizationPaymentMethodsResolver,
        },
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class OrganizationsRoutingModule {
}
