import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { OrganizationsUserResource } from '@core/http/organizations-user-resource.service';

@Injectable({
  providedIn: 'root',
})
export class OrganizationsUsersResolver implements Resolve<any> {

  constructor(
    private organizationsUserResource: OrganizationsUserResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.organizationsUserResource.index(route.parent.params.organization_id);
  }
}
